@font-face {
  font-family: "Press Start 2P";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Press Start 2P"), local("Press-Start-2P"),
    url("../fonts/press-start-2p-v9-latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/press-start-2p-v9-latin_cyrillic-regular.woff") format("woff");
}

html {
  box-sizing: border-box;
  font-size: $font-size;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  font-family: $font-family;
  font-weight: normal;
  line-height: $line-height;
  color: $text-color;
  background-color: $color-main;

  @media (min-width: $tablet-width) {
    font-size: $font-size--large;
  }

  @media (min-width: $desktop-width) {
    margin-top: 3rem;
    font-size: $font-size--large;
  }
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  word-wrap: break-word;
}

h1 {
  //font-size: $font-size-h1;
  //font-weight: 300;
}

h2 {
  //font-size: $font-size-h2;
  //font-weight: 300;
}

h3 {
  //font-size: $font-size-h3;
  //font-weight: 700;
}

h4 {
  //font-size: $font-size-h4;
  //font-weight: 700;
}

a {
  text-decoration: none;
  cursor: pointer;
}

fieldset {
  margin: 0;
  padding: 0;
}

blockquote {
  margin: 0;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
